@media screen and (max-width:980px) {
  .dashboard__grapchics-main,.dashboard__upper {
    flex-direction: column;
    display: flex;
    width: 80%;
  }
  .graphic {
    width: 100%;
  }
  .dashboard__graphics-header {
    width: 76%;
  }
  .grapchics-main__left-column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
  }
  .dashboard__graphics {
    display: flex;
    flex-direction: column;
    align-items: center;

  }
  
  .dashboard {
    align-items: center;
  }
  
  .grapchics-main__left-column {
    
    width: 90%;
  }
  .dashboard__middle {
    width: 100%;
    flex-wrap: wrap;
  }
  .dashboard__grapchics-main {
    align-items: center;
    display: flex;
    flex-direction: column;
    
      }
}

/* @media screen and(max-width:650px) {
  .dashboard__grapchics-main {

    width: 90%;
  } 
} */
@media screen and (max-width:800px) {
  .dashboard__grapchics-main,.dashboard__upper {
    flex-direction: column;
    width: 90%;
  }
  .dashboard {
    align-items: center;
  }
  
  
  .grapchics-main__left-column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
  }
  .dashboard__middle-item {
  
    height: 180px;
    width: 210px;
    padding: 10px 20px 15px 30px;
    

  }
  .dashboard__middle {
  width: 100%;
  flex-wrap: wrap;
  }
  .payment-info {
    margin-right: 0;
    margin-top: 10px;
  }
  /*
   .dashboard__grapchics {
    align-items: center;
  } */
}

@media screen and (max-width:467px) {
  .dashboard__middle {
    justify-content: center;
  }
  .grapchics-main__earn-number {
    font-size: 40px;
  }
  .dashboard__middle-item {
    height: 195px;
    width: 250px;
    margin-right: 0px;
   
  }
  .down-section__date,.down-section__status {
    font-size: 14px;
  }
  .down-section__status {
    padding: 6px 14px;
    margin-right: 10px;
  }
}
@media screen and (max-width:390px) {
  .down-section__date,.down-section__status {
    font-size: 11px;
  }
  .dashboard__title {
    line-height: 100%;
  }
  /* .dashboard__grapchics-main,.dashboard__upper {
    
    width: 98%;
  } */
}

@media screen and (max-width:343px) {
  .down-section__typeOf-payment {
    margin: 0 3px;
  }
.dashboard__grapchics-main {
  padding: 0 0 10px 0;
}
   .dashboard__title {
    margin-top: -35px;
    font-size: 15px;
    line-height: 100%;
  }
  .dashboard__filter-block {
    max-width: 110px;
  } 
  .dashboard__graphics-header select {
    font-size: 10px;
    width: 90%;
  }
  .dashboard__graphics-header {
    height: 96px;
  }
  .dashbard__date-inputs   {

  }
  .dashboard__down {
    width: 90% !important;
  }
}